import React from 'react';

const Text = {
    first: " Gress Photo Studio adalah sebuah tata usaha yang bergerak dibidang usaha fotografi, baik foto pernikahan, pasfoto, maupun foto pelajar instansi sekolah.",

    second:"Gress Photo Studio sudah berdiri sejak tahun 2002 dan sudah professional karena memiliki banyak sekali pengalaman dalam dibidang fotografi.",

    third:"Gress Photo Studio menyediakan berbagai layanan untuk memenuhi kebutuhan dokumen atau moment penting anda seperti cetak foto, cetak undangan, fotocopy, dll.",
};

const Profile = () => (
    <div className="container-profile">
        <div className="content-profile">
            <div className="gress">
                <h1>Profile</h1>
                <div className="border-profile"></div>
                <div className="text-profile">
                    <ul>
                        <li>{Text.first}</li><br/>
                        <li>{Text.second}</li><br/>
                        <li>{Text.third}</li>
                    </ul>
                    <img src={require("../img/12.jpg")} width={"35%"} alt=""/>
                </div>
                
            </div>
            <div className="owner">

            </div>
        </div>
    </div>
);

export default Profile;