import React from 'react';

const Contact = () => (
    <div className="container-contact">
        <div className="content-contact">
            <h1>Hubungi Kami</h1>
            <div className="border-contact"></div>
            <div className="display-contact">

                <div className="body-contact">
                    <div className="icon">
                        <i class="fa-solid fa-location-dot"></i>
                        <h2>Alamat</h2>
                    </div>
                    <div className="text">
                        <a 
                            href="https://www.google.com/maps/place/Gress+Photo+Studio/@-6.8500008,109.019748,21z/data=!4m12!1m6!3m5!1s0x2e6fb153fe98887d:0xd398f0052da5472e!2sGress+Photo+Studio!8m2!3d-6.8500326!4d109.0198318!3m4!1s0x2e6fb153fe98887d:0xd398f0052da5472e!8m2!3d-6.8500326!4d109.0198318" 
                            target="_blank" rel="noopener noreferrer">
                            Jalan K.H. Ahmad Dahlan, Dumeling, Wanasari, Brebes, Jawa Tengah
                        </a>
                    </div>
                </div>

                <div className="body-contact">
                    <div className="icon">
                        <i class="fa-brands fa-whatsapp"></i>
                        <h2>WhatsApp</h2>
                    </div>
                    <div className="text">
                        <a href="!#">+62 123456789</a>
                    </div>
                </div>

                <div className="body-contact">
                    <div className="icon">
                        <i class="fa-solid fa-envelope"></i>
                        <h2>Email</h2>
                    </div>
                    <div className="text">
                        <a href="!#">gressphoto@gmail.com</a>
                    </div>
                </div>
            </div>
        </div>
    </div>
);

export default Contact;