import React from 'react';


const Gallery = () => (
    <div className="container-gallery">
        <div className="content-gallery">
            <div className="gallery">
                <h1>Gallery</h1>
                <div className="border-gallery"></div>
                
                <div className="image-gallery">
                    <div className="column">
                        <img src={require("../img/1.JPG")} alt="manten1"/>
                        <img src={require("../img/2.JPG")} alt="manten3"/>
                        <img src={require("../img/3.JPG")} alt="manten2"/>
                    </div>

                    <div className="column">
                        <img src={require("../img/4.JPG")} alt="manten4"/>
                        <img src={require("../img/5.JPG")} alt="manten5"/>
                        <img src={require("../img/6.JPG")} alt="manten6"/>
                    </div>

                    <div className="column">
                        <img src={require("../img/7.JPG")} alt="manten7"/>
                        <img src={require("../img/8.JPG")} alt="manten8"/>
                        <img src={require("../img/9.JPG")} alt="manten9"/>
                    </div>
                </div>
            </div>
        </div>
        
    </div>
);

export default Gallery;