import React from 'react';

const Text = { 
    fotoStudio : "kami memiliki fasilitas studio foto untuk dokumentasi foto anda",
    
    perCetakan : "kami melayani cetak foto untuk memenuhi kebutuhan foto anda",

    fotoPernikahan : "kami melayani jasa foto pernikahan maupun pra pernikahan",
}

const Service = () => (
    <div className="container-service">
        <div className="content-service">
            <h1>Melayani</h1>
            <div className="border"></div>
            <div className="feature-service">
                
                <div className="card">
                    <i class="fa-solid fa-camera"></i>
                    <div className="text-card">
                        <h4>Foto Studio</h4>
                        <p>
                            {Text.fotoStudio}
                        </p>
                    </div>
                </div>

                <div className="card">
                    <i class="fa-solid fa-image"></i>
                    <div className="text-card">
                        <h4>Cetak Foto dan Undangan</h4>
                        <p>
                            {Text.perCetakan}
                        </p>
                    </div>
                </div>

                <div className="card">
                <i class="fa-solid fa-heart"></i>
                    <div className="text-card">
                        <h4>Foto Pernikahan</h4>
                        <p>
                            {Text.fotoPernikahan}
                        </p>
                    </div>
                </div>
            </div>
        </div>
    </div>
);

export default Service;