import React, { Component } from 'react';
import './App.css';
import { BrowserRouter as Router, Route, Link, Routes } from 'react-router-dom';

import Home from './pages/Home.js';
import Contact from './pages/Contact.js';
import Gallery from './pages/Gallery.js';
import Footer from './pages/Footer.js';
import Service from './pages/Service.js';
import Profile from './pages/Profile.js';
import Galleries from './pages/Galleries.js';

class App extends Component {
  render() {

    const navLink = () => {    
        const humburger = document.querySelector(".humburger");  
        const navMenu = document.querySelector(".navMenu");
        
        
        humburger.classList.toggle('toggle');
        if(navMenu.style.display === "block"){
          navMenu.style.display = "none";
        }
        else {
          navMenu.style.display = "block";
        }
      } 

    return (
      <Router>
      <div>
        <nav>
          <div className="container-nav">
            <div className="Logo">
              <Link to="/">
              <a className="" href="!#" style={{fontSize:"1.6rem"}}>Gress</a>
              </Link>
            </div>
            <div className="navMenu">
              <ul>
                <li>
                  <Link to="/">
                    <a href="!#">Home</a>
                    </Link>
                </li>
                <li>
                  <Link to="/Service">
                    <a href="!#">Service</a>
                  </Link>
                </li>
                <li>
                  <Link to="/Gallery">
                  <a href="!#">Gallery</a>
                  </Link>
                </li>
                <li>
                  <Link to="/Contact">
                    <a href="!#">Contact</a>
                  </Link>
                </li>
              </ul>
            </div>
            <div className="humburger" onClick={() => navLink()}>
                <div className="strip1"></div>
                <div className="strip2"></div>
                <div className="strip3"></div>
              </div>
          </div>
        </nav>
        <Routes>
          <Route path="/" element={<Home/>}></Route>
          <Route path="/Service" element={<Service/>}></Route>
          <Route path="/Gallery" element={<Gallery/>}></Route>
          <Route path="/Contact" element={<Contact/>}></Route>
          <Route path="/Profile" element={<Profile/>}></Route>
          <Route path="/Galleries" element={<Galleries/>}></Route>
        </Routes>
      </div>
      <Footer/>
      </Router>
    );
  }
}

export default App;
